/*----------------------------------------------------------------------------*/


#p2{
  width: 80vw;
  height: 100%;
  top: 0;
  z-index: 1;
  position: absolute;
}

.home{
  top: 0;
  width: 80vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start ;
  align-items: center;

}

.blocphoto{
  width: 80vw;
  height: 50vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.blochome{
  width: 60vw;
  height: 50vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  overflow-y: scroll;
  font-size: 1em;
  z-index: 3;
}

.blochome::-webkit-scrollbar {
  display: none;
  }



.example::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.example {
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}
