.contact{
  width: 50vw;
  height: 100vh;
  background-color: rgba(0,0,0,0.4);
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding-top: 20vh;
  font-size: 1em;
  font-family: 'Poppins', sans-serif;

}


.contact img{
  width: 50vw;
  bottom: 0;
  position: absolute;
}

.contact li{
  padding-bottom: 2vh;
}

.contact ul{
  list-style: none;
}
